	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.theregister.co.uk/content/6/24924.html\">The Register: Deutsche Bahn sues Google over anarchist links</a> (via <a href=\"http://en.nothingbutthetruth.de/archives/000776.shtml#000776\">Nothing But The Truth [en-US]</a>). The background is the regular shipments by rail of highly concentrated nuclear waste in (supposedly) highly secure Castor containers to a (supposedly) temporary storage facility at Gorleben near L端neburg. The shipments are accompanied by protests, massive police presence, actions to block the tracks, and the occasional act of sabotage. (I've mentioned my admiration of the <a href=\"/2001/03/28/\">armoured garbage trucks</a> before, and <a href=\"http://www.google.com/search?as_q=castor+gorleben&hl=en&btnG=Google+Search&as_epq=&as_oq=&as_eq=&lr=lang_en&as_ft=i&as_filetype=&as_qdr=all&as_occt=any&as_dt=i&as_sitesearch=&safe=images\">a Google search on Castor and Gorleben in English</a> will find lots of further information.)</p>\n<p>Now I'm all for free speech and all that, but there are only three rail routes to L端neburg, one of which goes through my town, and if anarchists would like to derail a Castor train as proof of concept that the shipments are unsafe, I'd much rather they do it as far away from my house as possible. For example, in <a href=\"http://www.citypaper.com/2002-02-20/mobs.html\">Baltimore</a> or <a href=\"http://www.lasvegassun.com/sunbin/stories/text/2002/feb/12/513027050.html\">Las Vegas</a>.</p>","fields":{"title":"Not In My Backyard","date":"19 Apr 2002","url":"https://papascott.de/2002-04-19-not-in-my-backyard/"}}},"pathContext":{"slug":"/2002-04-19-not-in-my-backyard/","prev":{"fields":{"title":"Plane Hits Pirelli Building in Milan","date":"18 Apr 2002","slug":"/2002-04-18-plane-hits-pirelli-building-in-milan/"}},"next":{"fields":{"title":"On Our Way!","date":"19 Apr 2002","slug":"/2002-04-19-on-our-way/"}}}}

